package models

import kotlinx.serialization.json.JsonNames


data class AudioFeatures (
    val danceability: Double,
    val energy: Double,
    val key: Long,
    val loudness: Double,
    val mode: Long,
    val speechiness: Double,
    val acousticness: Double,
    val instrumentalness: Double,
    val liveness: Double,
    val valence: Double,
    val tempo: Double,
    val type: String,
    val id: String,
    val uri: String,

    @JsonNames("track_href")
    val trackHref: String,

    @JsonNames("analysis_url")
    val analysisURL: String,

    @JsonNames("duration_ms")
    val durationMS: Long,

    @JsonNames("time_signature")
    val timeSignature: Long
) {

    companion object {
        fun fromDynamic(inD :dynamic) :AudioFeatures{
            return AudioFeatures(
                        inD.danceability,
                        inD.energy,
                        inD.key,
                        inD.loudness,
                        inD.mode,
                        inD.speechiness,
                        inD.acousticness,
                        inD.instrumentalness,
                        inD.liveness,
                        inD.valence,
                        inD.tempo,
                        inD.type,
                        inD.id,
                        inD.uri,
                        inD.track_href,
                        inD.analysis_url,
                        inD.duration_ms,
                        inD.time_signature,
            )
        }
        fun listFromDynamic(inListObj:dynamic) :List<AudioFeatures>{
            val playlistsout = mutableListOf<AudioFeatures>()



            for(featur in inListObj) {
                //sometimes there's a null that needs to be left out
                if(featur != null) {
                    val plkt = AudioFeatures.fromDynamic(featur)

                    playlistsout.add(plkt)
                }
            }
            //        it -> playlistsout.add(Playlist.fromDynamic(it))

            //playlistsout.forEach { console.info(it) }

            return playlistsout.toList<AudioFeatures>()
        }
    }
}
