package models

import kotlinx.serialization.json.JsonNames


data class Cursors (
    val after: String,
    val before: String
)

data class Item (
    @JsonNames
    val track: Track,

    @JsonNames("played_at", "playedAt")
    val playedAt: String,

    val context: Any? = null
)

data class Track (
    val album: Album,
    val artists: String,
    @JsonNames("duration_ms", "durationMS")
    val durationMS: Long,
    val explicit: Boolean,
    val href: String,
    val id: String,
    val name: String,
    val popularity: Long,
    @JsonNames("preview_url", "previewURL")
    val previewURL: String,
    val uri: String
){


    var audioFeatures :AudioFeatures? = null





    companion object {
        fun fromDynamic(inobjGeneric:dynamic) :Track{


            // json responses are a bit inconsistent between endpoints -
            //  there's sometimes a property hiding each track (even though the containing objects ARE the same object type),
            //  so this lets us abstract to our existing data models:
            val inobj = inobjGeneric.track ?: inobjGeneric

/*         // you can turn on these logs if you still need to understand:
            console.log("\ninobjgeneric:");
            console.log(inobjGeneric)
            console.log("\ninobjgeneric.track:");
            console.log(inobjGeneric.track)
*/




            var artistsStr = ""

            for(i in 0..inobj.artists.length-1)
                artistsStr += inobj.artists[i].name.toString() + ", "

            return Track(
                inobj.album,
                artistsStr,
                inobj.duration_ms,
                inobj.explicit,
                inobj.href,
                inobj.id,
                inobj.name,
                inobj.popularity,
                inobj.preview_url,
                inobj.uri

            )
        }

        fun listFromDynamic(inListObj:dynamic) :List<Track>{
            val tracksout = mutableListOf<Track>()
            //console.log("inlistobj:")
            //console.log(inListObj)
            for(t in inListObj.items) {
                //console.log(t)

                val plkt = Track.fromDynamic(t)
                tracksout.add(plkt)
            }
            //        it -> trackout.add(Track.fromDynamic(it))

            //trackout.forEach { console.info(it) }

            return tracksout.toList<Track>()
        }
    }
}

data class Album (

    val artists: List<Artist>,
    val href: String,
    val id: String,
    val images: List<Image>,
    val name: String,
    val uri: String
)

data class Artist (
    val id: String,
    val name: String,
    val uri: String
)




