package ui.shared

import kotlinx.html.*
import kotlinx.html.js.onClickFunction
import misc.js.Media
import models.Track

fun DIV.trackCard(t: Track) {
    onClickFunction = { ev ->
        Media.playSoundFromUrl(t.previewURL)
    }

    span("cardTitle") { +t.name }

    span("cardSubTitle") { +t.artists }

    br { style="clear: both;"}
    span { +"est. happiness:" }
    progress("trackCardProgressBar") {
        value = "${t.audioFeatures?.valence}"
        max = "1"
    }

}