package ui.shared

import kotlinx.browser.window
import kotlinx.html.*
import kotlinx.html.js.onClickFunction
import kotlinx.html.js.onTouchEndFunction
import kotlinx.html.js.onTouchStartFunction
import org.w3c.dom.*

fun <T> TagConsumer<HTMLElement>.dataListView(
    title: String,
    dataList :Collection<T>,
    cardEach: DIV.(cardData:T) -> Unit
) {

    div("dataViewHolder") {
        h4 {
            style = "background-color: rgba(88,88,105,0.25);" +
                    "padding-left: 3.6vw; padding-top: 0.6em; padding-bottom: 0.3em; margin-top: 0.1em; margin-left: 3px; margin-right: 4px; margin-bottom: 1px; border: 1px solid rgba(5,5,5,0.1); border-bottom: none;" +
                    """box-shadow: 3px 3px 0 4px rgba(3,3,1,0.1), 1px 2px 1px 1px rgba(13,13,11,0.75), inset 2px 2px 0 1px rgba(255,255,253,0.09);""".trimMargin()
            +title
        }
        div("dataviewlist") {


            dataList.forEach {
                div("card") {
                    onTouchStartFunction = {
                        it.target.asDynamic().style.boxShadow =
                            "inset 0.1em 0.1em 0.3em -0.03em rgba(20,20,20,0.6)"
                    }
                    onTouchEndFunction = {
                        it.target.asDynamic().style.boxShadow = "none";
                    }


                    cardEach(it)
                }
            }


        }
    }
}