package models

import kotlinx.serialization.json.JsonNames
import misc.TimeLength


data class Profile (

    val id: String,
    @JsonNames(names = ["display_name", "displayName"])
    val displayName: String,
    @JsonNames(names = ["_followers", "followers"])
    val followers: Int,
    val imageUrl: String,
    val uri: String
) {



    val recentlyPlayed : List<Track>? = null;
    var numPlaylists : Int? = null;


    var playlists   :List<Playlist>? = null

    val topTracks = mutableMapOf<TimeLength,List<Track>>(
        Pair( TimeLength.MONTH, listOf() ),
        Pair( TimeLength.YEAR, listOf() ),
        Pair( TimeLength.ALL_TIME, listOf() )
    )







    fun toJson() = JSON.stringify(this);







    companion object {
        public fun fromJson(json: String) = (JSON.parse<Profile>(json))
        public fun fromDynamic(d :dynamic) : Profile {
            val profOut =
                Profile(
                    d.id,
                    d.display_name,
                    d.followers.total,
                    d.images[0].url,
                    d.uri )
            return profOut;
        }
    }
}


