package models

import Requester
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.serialization.json.JsonNames
import org.w3c.dom.Audio


data class Playlist (
    val collaborative: Boolean,
    val description: String,
    val href: String,
    val id: String,
    val name: String,
    val public: Boolean,
    val uri: String,
    val numTracks: Int,
    val tracksLink :String,
    val imageUri :String,
    val ownerId :String

) {


    lateinit var tracksList : List<Track>




fun setTrackAudioFeatures(feats :List<AudioFeatures>){
    feats.forEach { feat ->
        tracksList.find { trac ->
            trac.id == feat.id
        }?.audioFeatures = feat;
    }
}

    init {

    }




    fun trackIDs() :List<String> {
        val listout = mutableListOf<String>()
        tracksList.forEach { listout.add(it.id) }
        return listout
    }


    companion object {

        fun listFromDynamic(inListObj:dynamic) :List<Playlist>{
            val playlistsout = mutableListOf<Playlist>()

            for(pl in inListObj.items) {
                val plkt = Playlist.fromDynamic(pl)

                playlistsout.add(plkt)
            }


            return playlistsout.toList<Playlist>()
        }




        fun fromDynamic(inObj:dynamic) :Playlist{

        return Playlist(
            inObj.collaborative,
            inObj.description,
            inObj.href,
            inObj.id,
            inObj.name,
            inObj.public,
            inObj.uri,
            inObj.tracks.total,
            inObj.tracks.href,
            inObj.images[1]?.url ?: inObj.images[0]?.url ?: "",
            inObj.owner.id ?: ""


        )
        }
    }

}

data class Image (
    val height: Long? = null,
    val url: String,
    val width: Long? = null
)
data class Tracks (
    val href: String,
    val total: Long
)
